.room-code {
    height: 40px;
    border-radius: 8px;
    overflow: hidden;

    background: #fff;
    border: 1px solid #835afd;
    cursor: pointer;

    display: flex;

    div {
        background: #835afd;
        padding: 0 12px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    span {
       display: block;
       align-self: center;
       flex: 1;
       padding: 0 12px 0 10px;
       width: 200px;
       font-size: 12px;
       font-weight: 500; 
    }
}